.sidebar {
    flex: 0.35;
    display: flex;
    flex-direction: column;
}

img {
    width: 40px;
    object-fit: contain;
}

.material-symbols-outlined {
    font-size: 28px;
}

.sidebarHeader {
    display: flex;
    justify-content: space-between;
    padding: 10px;
    border-right: 1px solid lightgray;
}

.sidebarHeaderRight {
    padding: 5px;
    min-width: 10vw;
}

.sidebarSearch {
    display: flex;
    align-items: center;
    background-color: #f6f6f6;
    height: 40px;
    padding: 10px;
}

.sidebarSearchContainer {
    display: flex;
    align-items: center;
    background-color: white;
    width: 100%;
    height: 35px;
    border-radius: 20px;
}

.sidebarSearchContainer>input {
    border: none;
    outline-width: 0;
    margin-left: 10px;
}